

const initialState = {

    allCountries: [],
    saludo: ""

};


function rootReducer(state = initialState, action) {

    if (action.type === "GetAll") {
        return {
            ...state,
            allCountries: [action.payload]
        }
    }
    if (action.type === "saludame") {
        return {
            ...state,
            saludo: action.payload
        }
    }
    return {
        ...state
    }
}

export default rootReducer;


